	function uploadDataToServer(thiz) {
	  console.log("uploading data");
	  var d = new Date();
	  var n = d.getTime();
	  var ztate = thiz.state;
	  var version = n - thiz.state.participantid;
	  var participantid = thiz.state.participantid;
	  var broll_list = JSON.stringify(thiz.state.broll_list);
	  console.log(broll_list);
	  var queries = JSON.stringify({ queries: thiz.state.queries });
	  var usertype = ztate.usertype;
	  var uitype = ztate.uitype;
	  var videotype = ztate.videotype;
	  var apiURL = 'https://dapptalk.org/bcreate.php';
	  var formData = {
	    version: version,
	    participantid: participantid % 10000000,
	    broll_list: broll_list,
	    queries: queries,
	    usertype: usertype,
	    uitype: uitype,
	    videotype: videotype
	  };
	  fetch(apiURL, {
	    method: 'POST',
	    mode: 'no-cors',
	    headers: {
	      'Accept': 'application/json',
	      'Content-Type': 'application/json',
	      'Access-Control-Allow-Origin': '*'
	    },
	    body: JSON.stringify({
	      formData: formData
	    })
	  }).then(function (response) {
	    return response.text();
	  }).then(function (responseData) {
	    console.log("response: ");console.log(responseData);
	  }).catch(function (err) {
	    console.log(err);
	  });
	}
	
	function TranscriptViewVeryIntelligent(props) {
	  var thiz = props.context;
	  var table = [];
	  var words = props.context.state.words;
	  var N = words.length;
	  var pauses_pos = 0;
	  var done_pauses = false;
	  var counter_nonbreak = 0;
	  var show_recommendations = props.context.state.uitype == "i";
	
	  var broll_list = props.context.state.broll_list;
	  var num_added_broll = broll_list.length;
	
	  var current_recommendation_style = thiz.state.current_recommendation_style;
	  var concepts = thiz.state.concepts;
	
	  if (current_recommendation_style == "off") {
	    concepts = [];
	  }
	  if (current_recommendation_style == "audio") {
	    concepts = thiz.state.concepts_audio;
	  }
	  if (current_recommendation_style == "visual") {
	    concepts = thiz.state.concepts_video;
	  }
	  if (current_recommendation_style == "text") {
	    concepts = thiz.state.concepts_text;
	  }
	  if (current_recommendation_style == "fast") {
	    concepts = thiz.state.concepts_fast;
	  }
	
	  for (var i = 0; i < N; i = i + 1) {
	    var word = words[i];
	    var next_word_is_period = words[Math.min(N - 1, i + 1)].word == ".";
	    var future_time = words[Math.min(N - 1, i + 1)].start;
	    var prev_time = words[Math.max(0, i - 1)].start;
	    var current_time = word.start;
	    var text = word.word;
	    var keywords = thiz.state.global_keywords;
	    var is_kw = keywords.indexOf(word.word) >= 0;
	    var this_concept = "";
	    var currently_in_concept = false;
	    var concept_start = 0;
	    var suggested_duration = 4000;
	    var first_in_concept = false;
	    for (var j = 0; j < concepts.length; j++) {
	      var start_concept = concepts[j][1];
	      var end_concept = concepts[j][1] + concepts[j][2];
	      if (current_time > start_concept && current_time < end_concept) {
	        concept_start = start_concept * 1000;
	        currently_in_concept = true;
	        this_concept = concepts[j][0];
	        suggested_duration = concepts[j][2] * 1000;
	        if (prev_time <= start_concept) {
	          first_in_concept = true;
	        }
	        break;
	      }
	    }
	    if (!show_recommendations) {
	      currently_in_concept = false;
	    }
	    var broll_start = 0;
	    var currently_in_broll = false;
	    var broll_id = 0;
	    var first_broll_word = false;
	    var broll_thumbnail = "";
	    var last_broll_word = false;
	
	    for (var _j = 0; _j < num_added_broll; _j++) {
	      var start_broll = broll_list[_j].start;
	      var end_broll = start_broll + broll_list[_j].duration / 1000;
	      if (current_time > start_broll && (current_time <= end_broll || prev_time <= start_broll)) {
	        broll_thumbnail = broll_list[_j].thumbnail;
	        broll_start = start_broll * 1000;
	        broll_id = broll_list[_j].id;
	        currently_in_broll = true;
	        if (prev_time <= start_broll) {
	          first_broll_word = true;
	        }
	        if (future_time > end_broll) {
	          last_broll_word = true;
	        }
	        break;
	      }
	    }
	
	    if (!("." == text)) text = " " + text;
	
	    if (future_time > thiz.state.currentTimeAroll / 1000 && current_time <= thiz.state.currentTimeAroll / 1000) {
	      /*
	        CURRENT POSITION
	      */
	      if (first_broll_word) {
	        //on mouse down it should register current broll index
	        //on mouse up it should update the position of the currently registered broll
	        table.push(_react2.default.createElement('img', { src: broll_thumbnail, className: 'grab', alt: 'Broll thumbnail', width: '20', height: '20', draggable: 'true', onDragStart: thiz.brollDragStartHandler(broll_id) }));
	      }
	      table.push(_react2.default.createElement(
	        'span',
	        { key: i, className: 'bg-primary text-white text-transcript', onClick: props.context.clickTranscriptIntelligent(current_time * 1000, i, ""), onContextMenu: thiz.firstMethod(text) },
	        text
	      ));
	    } else {
	      /*
	        B-ROLL
	      */
	      if (currently_in_broll) {
	        if (first_broll_word) {
	          //on mouse down it should register current broll index
	          //on mouse up it should update the position of the currently registered broll
	          table.push(_react2.default.createElement('img', { src: broll_thumbnail, className: 'grab', alt: 'Broll thumbnail', width: '20', height: '20', draggable: 'true', onDragStart: thiz.brollDragStartHandler(broll_id) }));
	        }
	        if (last_broll_word) {
	          table.push(_react2.default.createElement(
	            'span',
	            { className: 'bg-success text-transcript border-4 border-right border-dark grab', draggable: 'true', onDragStart: thiz.brollDragDurationHandler(broll_id) },
	            text
	          ));
	        } else {
	          table.push(_react2.default.createElement(
	            'span',
	            { key: i, draggable: 'true', className: 'bg-success text-transcript', onDragOver: thiz.preventDefault(), onDrop: thiz.brollDropHandler2(prev_time, "onlydurationchange") },
	            text
	          ));
	        }
	      } else if (currently_in_concept) {
	        table.push(_react2.default.createElement(
	          'span',
	          { key: i, className: 'bg-info text-transcript', 'data-tip': this_concept, onClick: props.context.getImagesByQueryTerm(this_concept, prev_time, suggested_duration), draggable: 'true', onDragOver: thiz.preventDefault(), onDrop: thiz.brollDropHandler(prev_time) },
	          text
	        ));
	        if (first_in_concept) {
	          table.push(_react2.default.createElement(
	            'button',
	            { className: 'btn btn-outline-secondary btn-concept', onClick: props.context.getImagesByQueryTerm(this_concept, prev_time, suggested_duration) },
	            this_concept
	          ));
	        }
	      } else {
	        /*
	          UNHIGHLIGHTED
	        */
	        table.push(_react2.default.createElement(
	          'span',
	          { key: i, draggable: 'true', className: 'text-transcript', onDragOver: thiz.preventDefault(), onDrop: thiz.brollDropHandler(prev_time), onClick: props.context.clickTranscriptIntelligent(current_time * 1000, i, ""), onContextMenu: thiz.firstMethod(text) },
	          text
	        ));
	      }
	    }
	    if (word.word == ".") {
	      if (counter_nonbreak > 15) {
	        table.push(_react2.default.createElement('div', { className: 'textbreak' }));
	        counter_nonbreak = 0;
	      }
	    }
	    counter_nonbreak += 1;
	  }
	  return table;
	}
	
