  brollDropHandler = (current_time) => (event) => {
    event.preventDefault();
    var drag_obj = JSON.parse(event.dataTransfer.getData('text'));
    var dragged_broll_id = drag_obj.broll_id;
    var drag_action = drag_obj.action;
    var broll_list = this.state.broll_list;
    var N = broll_list.length;

    console.log("received drop event with data",dragged_broll_id)
    console.log("the drop location is",current_time)
    if (drag_action == 0) {
      //this updates the starting point of this specific added broll
      for (let i = 0;i < N; i++) {
        if (broll_list[i].id == dragged_broll_id) {
          broll_list[i].start = current_time;
          break
        }
      }
      this.setState({broll_list: broll_list})
      uploadDataToServer(this)
    } else {
      //this updates the duration of this specific added broll
      for (let i = 0;i < N; i++) {
        if (broll_list[i].id == dragged_broll_id) {
          var new_duration = current_time - broll_list[i].start
          console.log(new_duration,"new duration")
          if (new_duration > 0){
            broll_list[i].duration = new_duration*1000;
            console.log(broll_list)
            this.setState({broll_list: broll_list})
            uploadDataToServer(this)
          }
          break
        }
      }
    }
  }

