  callingUpdate = () => (event) => {
    console.log("callingUpdate")
    const broll_list = this.state.broll_list;
    if (this.state.justclicked) {
      prev_pos = event.target.currentTime;
    }
    const N = broll_list.length;
    for (let i = 0;i < N; i++) {
      if (broll_list[i].start >= prev_pos && broll_list[i].start <= event.target.currentTime) {
        showThisBroll("broll_"+i,broll_list[i].duration)
        break;
      }
    }
    prev_pos = event.target.currentTime;
    var date = new Date(null);
    date.setSeconds(event.target.currentTime); // specify value for SECONDS here
    var timestamp = date.toISOString().substr(14, 5);
    this.setState({timestamp: timestamp, currentTimeAroll: event.target.currentTime*1000, justclicked: false})
  }

