  addNow = (url,thumbnail,thisranking) => (event) => {
    var current_time = this.state.currentTimeAroll;
    console.log(this.state.suggested_duration)
    //check if we are currently in b-roll, then don't do anything
    var broll_list = this.state.broll_list;
    console.log("broll length",broll_list.length)
    var currently_in_broll = false;
    for (let i=0;i<broll_list.length;i++) {
      var start = broll_list[i].start
      var end = start + broll_list[i].duration/1000
      console.log(current_time,start-100,end)
      if (current_time/1000 >= start-100 && current_time/1000 < end) {
        console.log('we are currently in a B=roll so wont add this new broll. end.')
        return
      }
    }
    var actual_duration = this.state.suggested_duration
    if (!(actual_duration == 4000)) {
      actual_duration = actual_duration + 200;
    }
    var query = this.state.searchkey;
    var broll_obj = {"start":current_time/1000-0.05,"duration":actual_duration,"rank":thisranking,"url":url,"thumbnail":thumbnail,"id":url,"query":query};
    console.log(current_time/1000,this.state.suggested_duration,thisranking,query)
    broll_list.push(broll_obj);
    this.setState({broll_list:broll_list})
    uploadDataToServer(this)
  }

